<template>
  <div class="ac-esign-toolbar">
    <b-btn-group class="w-100" v-if="show_save_button"> </b-btn-group>

    <div v-if="!is_public" class="mt-3">
      <h3 class="ml-2">Add</h3>
      <b-list-group class="">
        <b-list-group-item v-for="tool in simple_tools" :key="`simple_${tool.id}`" :data-tool_id="tool.id" class="ac-esign-toolbar_item p-2" ref="tools" draggable="true">
          <div class="p-1">
            <h3 :class="[tool.icon]" class="text-muted" v-b-tooltip.hover :title="`${tool.label}`"></h3>
          </div>
        </b-list-group-item>
      </b-list-group>
    </div>
    <hr v-if="!is_public" />
    <div v-if="!is_public" class="mt-3">
      <h3 class="ml-2">Restrictions</h3>
      <b-list-group-item
        v-for="tool in resctriction_tools"
        :key="`resctriction_${tool.id}`"
        :data-tool_id="tool.id"
        class="ac-esign-toolbar_item ac-esign-toolbar_restrictions p-2"
        ref="tools"
        draggable="true"
      >
        <div class="p-1">
          <h3 :class="[tool.icon]" class="text-muted" v-b-tooltip.hover :title="`${tool.label}`"></h3>
        </div>
      </b-list-group-item>
      <div class="mt-3 ml-2 ac-esign-toolbar_help">
        <ul class="pl-0 mt-3">
          <li class="mb-2">
            <span class="text-dark font-weight-bold">Note:</span>
            (Restrictions cannot be edited)
          </li>
        </ul>
      </div>
    </div>
    <hr v-if="!is_public" />
    <h4 class="pt-2 ml-2">Use this key for manipulating of tools:</h4>
    <div class="mt-3 ml-2 ac-esign-toolbar_help">
      <ul class="pl-0 mt-3">
        <!-- <li class="mb-2">
          <span class="text-dark font-weight-bold"><span class="fe fe-trash-2 mr-2"> </span> Delete</span>
          (for deleting object/item)
        </li> -->
        <li class="mb-2">
          <span class="text-dark font-weight-bold"><span class="fe fe-x-circle mr-2"> </span> Esc</span>
          (for cancel object/item)
        </li>
        <li class="mb-2">
          <span class="text-dark font-weight-bold"><span class="fe fe-corner-down-left mr-2"> </span>Enter</span>
          (for enter value object/item)
        </li>
      </ul>
    </div>

    <b-button v-if="show_save_button" class="w-100" :id="`popover-form_${_uid}`" @click="save" variant="primary">Save</b-button>
    <b-popover :show.sync="popover" :target="`popover-form_${_uid}`" triggers="focus" placement="top"> Saved! </b-popover>
  </div>
</template>

<script>
  /**
   * Ac Esign Toolbar - draggable tools into pdf viewer
   *
   * @event preview
   * @event upload
   * @event save
   */
  export default {
    name: "AcEsignToolbar",

    props: {
      /**
       * Disable users dropdown & tools
       */
      is_public: {
        type: Boolean,
        required: false,
        default: false,
      },
      tools: {
        type: Object,
        default: () => ({}),
      },

      users_list: {
        type: Array,
        default: () => [],
      },

      value: {
        type: Object,
        default: () => ({}),
      },

      show_save_button: {
        type: Boolean,
        default: false,
      },
    },

    computed: {
      simple_tools() {
        return this.tools.add ? this.tools.add : [];
      },

      resctriction_tools() {
        return this.tools.restrictions ? this.tools.restrictions : [];
      },

      toolbar_users_list() {
        let list = this.users_list;

        if (!this.$parent.admin) {
          list = [this.$parent.selected_user];
        }

        return list;
      },
    },

    mounted() {
      this.add_drag_event_listeners();
    },

    destroyed() {
      this.remove_drag_event_listeners();
    },

    data() {
      return {
        popover: false,
      };
    },

    methods: {
      /**
       * Set drag listeners for tools
       */
      add_drag_event_listeners() {
        if (!this.$refs.tools) return;
        this.$refs.tools.forEach((tool) => {
          tool.addEventListener("dragstart", this.tool_drag_start);
        });
      },

      /**
       * Remove drag listeners for tools
       */
      remove_drag_event_listeners() {
        if (!this.$refs.tools) return;
        this.$refs.tools.forEach((tool) => {
          tool.removeEventListener("dragstart", this.tool_drag_start);
        });
      },

      /**
       * Triggered on drag start on tool
       * Set tool options to event
       * @param {DragEvent} event
       */
      tool_drag_start(event) {
        const all_tools = [...this.simple_tools, ...this.resctriction_tools];
        const tool = all_tools.find(({ id }) => id.toString() === event.target.dataset.tool_id);
        event.dataTransfer.setData("value", JSON.stringify({ ...tool.item, id: tool.id }));
        event.dataTransfer.setData(`json:${JSON.stringify(tool.item)}`, true);
      },

      /**
       * Emit 'save' event
       */
      save() {
        this.popover = true;

        setTimeout(() => {
          this.popover = false;
        }, 2000);

        this.$emit("save");
      },
    },
  };
</script>
